/* reset */
* {
  padding: 0; margin: 0;
}
a {
  text-decoration: none;
}
li {
  list-style: none;
}

/* 레이아웃 */

.wrap {
  width: 1120px;
  margin: 0 auto;
}
.navbar {
  height: 80px;
}
.main {
  min-height: 550px;
  padding: 50px 0;
}
.footer {
  background-color: #000;
  color: #fff
}

/* 네브 */
.nav-menu ul {
  display: flex;
  justify-content: space-between;
}
.nav-menu li a {
  color: #000;
  font-size: 17px;
  font-weight: bold;
  line-height: 80px;
  padding: 20px;
}

/* 헤더 */
.page-title {
  text-align: center;
  padding: 1.2em 0;
  border-bottom: 2px solid #000;
}

/* 메인 */
.entry-content {
  text-align: center;
}
.entry-content h3 {
  font-size: 22px;
  padding: 20px 0;
  margin-bottom: 50px;
}
.entry-content p {
  margin-bottom: 20px;
}

/* 푸터 */
.footer .footer-widgets {
  padding: 20px 0 20px 0;
  text-align: center;
}
.footer .social-links {
  margin: 0 auto;
  width: 50%;
  display: flex;
  justify-content: space-between;
}
.footer .social-links li {

}
.footer .social-links li a {
  color: #fff;
}
.site-infos {
  text-align: center;
  font-size: 12px;
  padding: 0 0 20px 0;
}